  -webkit-focus-ring-color: rgba(255, 255, 255, 0);
  cursor: default; }

body {
  background: #2c3136;
  font-size: 100%;
  line-height: 1;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased; }

[placeholder]:focus::placeholder {
  color: transparent; }

.App {
  margin: 50px auto;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  color: white; }

.wrapper {
  max-width: 400px;
  margin: 0 auto; }

h1 {
  font-weight: 700;
  text-align: center; }

.search {
  text-align: center; }

.search__input {
  width: 100%;
  padding: 12px 50px 10px 16px;
  font-size: 17px;
  border-radius: 4px;
  box-shadow: 0px 0px 11px 4px #3f51b5;
  border: 1px solid #7d8888;
  transition: all .5s ease; }

.search__status {
  margin: 15px auto;
  font-size: 20px; }

.post {
  width: 100%;
  margin: 15px auto;
  padding-bottom: 10px;
  border-bottom: 1px solid #505050; }
  .post__title {
    margin-bottom: 10px;
    font-size: 20px;
    font-weight: 700; }
    .post__title a {
      color: #1d85ca;
      text-decoration: none;
      transition: color .3s ease; }
      .post__title a:hover {
        color: #0027ff; }
  .post__counter {
    background-color: rgba(245, 245, 246, 0.08);
    border-radius: 20px;
    color: #cbcbcb;
    display: inline-block;
    font-size: 12px;
    font-weight: 600;
    line-height: 1;
    padding: 4px 5px; }
  .post__text {
    font-size: 16px;
    margin: 10px 15px 15px 0;
    padding-bottom: 10px; }

.result {
  margin: 25px auto 0 auto;
  max-width: 400px;
  height: 400px;
  overflow: auto; }

.result::-webkit-scrollbar-button {
  width: 6px;
  height: 0px; }

.result::-webkit-scrollbar-track {
  border-radius: 5px;
  background-color: #32312e;
  box-shadow: 0px 0px 3px #000 inset; }

.result::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: rgba(63, 81, 181, 0.4); }

.result::-webkit-resizer {
  width: 7px;
  height: 0px; }

.result::-webkit-scrollbar {
  width: 11px; }

